<template>
	<div id="agent-finance-index">
		<public-header title="申请提现"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">
						账户可用余额:
						<span class="c-primary margin-5-l">¥{{agent.amount}}</span>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t padding-15">
				<p class="font-14 margin-10-b">提现金额</p>
				<input v-model.number="get_fee" type="text" class="getcash-num" placeholder="请输入提现金额">
			</van-cell-group>
			<div class="padding-15">
				<van-button size="normal" type="primary" :disabled="!islegal" block class="font-16" @click="onSubmit">提现</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				agent: {
					amount: 0,
				},
				get_fee: '',
				showPreLoading: true,
				islegal: false
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'plateform/agent/finance',
					autoAssign: true,
					variable: 'agent',
					success: function(res) {
						that.islegal = true;
					}
				})
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				let get_fee = parseFloat(that.get_fee);
				if(isNaN(get_fee) || get_fee <= 0) {
					that.util.$toast('提现金额有误');
					return false;
				}
				if(get_fee > that.agent.amount) {
					that.util.$toast('提现金额不能大于账户可用余额');
					return false;
				}
				that.islegal = false;
				setData({
					vue: that,
					url: 'plateform/agent/finance/getcash',
					data: {
						get_fee: get_fee
					},
					message: '申请提现成功,等待平台管理员审核',
					redirect: that.util.getUrl({path: '/pages/plugin/agent/finance/index'}),
					fail: (res) => {
						that.util.$toast(res);
						that.islegal = true;
						return;
					}
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#agent-finance-index .getcash-num{
		border:1px solid #e7e7e7;
		border-radius:3px;
		font-size:14px;
		padding:1px 0 1px 6px;
		height: 34px;
		width: 100%;
		display: block;
	}
	#agent-finance-index .getcash-rule{
		color: #999;
		position: relative;
		padding-left: 10px;
		font-size: 14px;
		margin-top: 10px;
	}
	#agent-finance-index .getcash-rule:before{
		content: "";
		width: 5px;
		height: 5px;
		border-radius: 50%;
		position: absolute;
		left: 0;
		background: #ccc;
		top: 50%;
		margin-top: -2.5px;
	}

</style>


